body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.template-container {
  background-color: #fff;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  width: 260px;
  min-height: 110px;
  max-height: fit-content;
  margin: 15px auto;
}

.container {
  padding: 15px;
  font-size: 14px;
  display: flex;
  flex-direction: column;
  align-content: space-between;
  font-weight: 500;
}

.id-pic {
  display: flex;
  justify-content: space-between;
}

.template-id {
  color: grey;
}

.template-title {
  margin-top: 5px;
  height: 40px;
  margin-bottom: 5px;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.template-priority {
  height: 18px;
  border-radius: 3px;
}

.template-tags {
  display: flex;
  align-items: center;
}

.template-tag {
  display: flex;
  align-items: baseline;
  border-radius: 5px;
  border: 1px solid rgb(214, 214, 214);
  margin-left: 10px;
}

.tag {
  margin-right: 5px;
}

.user_profile {
  border-radius: 50px;
}


.dropdown-container {
  
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: baseline;
  width: 90vw;
  margin: 0 auto;
}

.dropdown {
  justify-content: center;
  max-width: 100px;
  height: 30px;
  margin-top: 1vw;
  background: none;
  border: none;
  border-radius: 5px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  outline: none;
  cursor: pointer;
  display: flex;
  align-items: center;
}

.dropdown img {
  margin: 5px;
}
.dropdown p {
  font-weight: bold;
}

.new-container {
  width: 250px;
  margin-top: 10px;
  background-color: #f8f8fa;
  border-radius: 5px;
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.selection-container {
  padding: 10px 0;
}

.selection {
  display: flex;
  width: 80%;
  margin: 0 auto;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  padding: 10px auto;
}

.selection p {
  font-size: 14px;
  font-weight: 500;
  color: grey;
}

select {
  border: 1px solid rgb(216, 216, 216);
  border-radius: 5px;
  padding: 0 5px;
  outline: none;
}


body {
  background-color: #f4f5f8;
}

.parent {
  display: flex;
  width: 90vw;
  margin: 0 auto;
  padding-top: 7vh;
  flex-wrap: wrap;
}

.box {
  margin: 15px 1vw 0 0;
}

.box-header {
  display: flex;
  font-weight: bold;
  font-size: 14px;
  display: flex;
  justify-content: space-between;
}

.statdot {
  height: 10px;
  width: 10px;
  margin: 0 5px;
  background-color: #ccc;
  border-radius: 50%;
  display: inline-block;
}

.todo {
  border: 2px solid #ccc;
  background: none;
}

.inprog {
  background-color: #f0c949;
}

.backlog {
  background-color: #a71a1a;
}

.done {
  background-color: #5e6ad2;
}

.cancel {
  background-color: black;
}

img {
  
  margin-right: 8px;
}

.header-content {
  display: flex;
  align-items: center;
}

.user-name {
  margin-right: 10px;
  font-weight: 500;
}

.user-count {
  font-weight: 500;
  color: grey;
}

.btn-holder {
  display: flex;
  align-items: center;
}

.btn {
  cursor: pointer;
  background: none;
  border: none;
  margin: 0 auto;
  font-size: large;
  color: grey;
}

.icon {
  padding-bottom: 10px;
}

.ham.btn {
  padding-bottom: 10px;
}


